import axios from "axios";

export default async function PostDetail({ params }) {
  const { id } = params;

  try {
    const res = await axios.get(`http://localhost:8000/api/blogs/${id}`, {
      // Avoid caching to always fetch the latest content
      headers: { "Cache-Control": "no-store" },
    });

    const post = res.data;

    return (
      <main className="container py-5">
        <h1 className="mb-4">{post.title}</h1>
        <p>{post.content}</p>
        <a href="/posts" className="btn btn-outline-primary mt-4">
          ‚Üê Back to Posts
        </a>
      </main>
    );
  } catch (error) {
    console.error("Error loading post:", error.message);

    return (
      <div className="container py-5 text-center">
        <h3 className="text-danger">Post not found</h3>
      </div>
    );
  }
}
